import React, { useState } from "react";
import "../css/register.css";
import "../css/login.css";
import "../css/findAccount.css";
import { Link } from "react-router-dom";
import Select from "react-select";

const languageOptions = [
  { value: "korean", label: "" },
  { value: "english", label: "English" },
  { value: "japanese", label: "" },
];

const FindAccount = () => {
  const [email, setemail] = useState("");
  const handleemailChange = (e) => {
    setemail(e.target.value);
  };
  const [language, setLanguage] = useState(languageOptions[0]); //   
  const handleLanguageChange = (selectedOption) => {
    setLanguage(selectedOption);
  };
  const customStyles = {
    indicatorContainer: (provided) => ({
      ...provided,
      paddingLeft: 0,
      paddingRight: 0,
    }),
    // control: , singleValue: , option: , menu:
    singleValue: (styles) => ({
      ...styles,
      color: "#FFFFFF",
      fontSize: "15px",
    }),
    control: (styles) => ({
      ...styles,
      display: "flex",
      minWidth: "107px",
      minHeight: "46px",
      justifyContent: "center",
      alignItems: "center",
      gap: "2px",
      borderRadius: "6px",
      backgroundColor: "#3C3C3C",
      border: "1px",
      boxSizing: "border-box",
      padding: "0 8px",
    }),
    option: (styles) => ({
      ...styles,
      padding: 0,
    }),

    indicatorSeparator: () => ({
      display: "none", //    display none 
    }),
    menu: (styles) => ({
      ...styles,
      backgroundColor: "#292929;",
    }),
    valueContainer: (styles) => ({
      ...styles,
      padding: 0,
      textAlign: "right",
    }),
  };
  return (
    <div className="background">
      <div className="Find_find-form">
        {" "}
        <mini-container>
          <div className="Login_textfields">
            <div className="container_input">
              <span className="input_title"></span>
              <div>
                <input
                  className="Login_textfield"
                  type="text"
                  id="email"
                  autoComplete="off"
                  placeholder=""
                  value={email}
                  onChange={handleemailChange}
                />
              </div>
            </div>
          </div>
          <div className="Login_buttons">
            {" "}
            <button className="orange_btn"> </button>
            <button className="grey_btn">
              <Link to="/login" className="register_text">
                 
              </Link>
            </button>
          </div>
        </mini-container>
        <div className="Login_border-row"></div>
        <mini-container>
          <div>
            <div className="Find_language">
              <div>
                <Select
                  value={language}
                  onChange={handleLanguageChange}
                  options={languageOptions}
                  styles={customStyles} //   
                />
              </div>
            </div>
            <p className="Login_alert">
               ,    .  
                    .
            </p>
          </div>
        </mini-container>
      </div>
    </div>
  );
};

export default FindAccount;
