import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import Modal from "./Slide_modal";

export default function ModalImage(props) {
  const { settings } = props; // props   .
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedImage, setSelectedImage] = useState("");

  const openModal = (imageLinkone) => {
    setSelectedImage(imageLinkone);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };
  const defaultSetting = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: false,
    draggable: false, //  
  };
  const images = props.images || []; // props       

  const mergedSettings = { ...defaultSetting, ...settings }; //   props   .

  return (
    <div>
      <Slider className="slide_copy-imgs" {...mergedSettings}>
        {images.map((urlone, index) => (
          <div key={index}>
            <img
              src={urlone}
              alt=""
              className="copy_img_120"
              onClick={() => openModal(urlone)}
            />
          </div>
        ))}
      </Slider>
      {isModalOpen && <Modal imageUrl={selectedImage} onClose={closeModal} />}
    </div>
  );
}
