export function NextArrow(props) {
  const { className, style, onClick } = props;

  return (
    <div
      className={className}
      style={{
        ...style,
        display: "flex",
        justifyContent: "center", //    
        alignItems: "center",
        backgroundColor: "rgba(34, 34, 34, 0.5)", //   
        zIndex: "800",
        height: "102%", //    100% 
        width: "88px", // SVG   
      }}
      onClick={onClick}
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="88"
        height="120"
        viewBox="0 0 88 120"
        fill="none"
      >
        <rect opacity="0" width="88" height="120" fill="#222222" />
        <path
          opacity="1"
          d="M59 60.5L37.25 73.0574L37.25 47.9426L59 60.5Z"
          fill="#FEB47B"
        />
      </svg>
    </div>
  );
}

export function PrevArrow(props) {
  const { className, style, onClick } = props;

  return (
    <div
      className={className}
      style={{
        ...style,
        display: "flex",
        justifyContent: "center", //    
        alignItems: "center",
        backgroundColor: "rgba(34, 34, 34, 0.5)", //   
        zIndex: "800",
        height: "102%", //    100% 
        width: "88px", // SVG   
      }}
      onClick={onClick}
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="88"
        height="120"
        viewBox="0 0 88 120"
        fill="none"
      >
        <rect opacity="0" width="88" height="120" fill="#222222" />
        <path
          opacity="1"
          d="M59 60.5L37.25 73.0574L37.25 47.9426L59 60.5Z"
          fill="#FEB47B"
          transform="rotate(180 44 60)"
        />
      </svg>
    </div>
  );
}
