import "../css/home_combine.css";

import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import Select from "react-select";
import axios from "axios";

const Resister = () => {
  const navigate = useNavigate();
  const inputLabels = {
    user_id: "",
    password: "",
    name: "",
    phone: "",
    nickname: "",
    email: "",
  };
  const initialInputs = {
    user_id: "",
    password: "",
    name: "",
    phone: "",
    nickname: "",
    email: "",
  };
  const [inputs, setInputs] = useState(initialInputs);
  const [errors, setErrors] = useState({});

  const validateInputs = () => {
    const newErrors = {};

    // user_id  
    if (!inputs.user_id.match(/^[a-zA-Z0-9!@]{1,10}$/)) {
      newErrors.user_id =
        " 10   , , !  @ .";
    }

    // password  
    if (inputs.password.length > 16 || inputs.password.length === 0) {
      newErrors.password = " 16  .";
    }

    // name  
    if (!inputs.name.match(/^[-a-zA-Z--\s]{1,10}$/)) {
      newErrors.name =
        " , ,   10  .";
    }

    // phone  
    if (!inputs.phone.match(/^\d{1,11}$/)) {
      newErrors.phone = "  11  .";
    }

    // nickname  
    if (inputs.nickname.length > 16 || inputs.password.length === 0) {
      newErrors.nickname = " 11  .";
    }

    // email  
    if (!inputs.email.match(/^[\w.-]+@[A-Za-z]+\.[A-Za-z]+$/)) {
      newErrors.email = "   .";
    }

    setErrors(newErrors);

    const isValid = Object.keys(newErrors).length === 0;
    return isValid;
  };
  const handleInputChange = (e) => {
    const { name, value } = e.target;

    // input     
    console.log(`${name}: ${value}`);

    // phone     
    if (name === "phone" && !isNaN(value)) {
      setInputs({
        ...inputs,
        [name]: value,
      });
    } else if (name !== "phone") {
      setInputs({
        ...inputs,
        [name]: value,
      });
    }
  };

  const languageOptions = [
    { value: "korean", label: "" },
    { value: "english", label: "English" },
    { value: "japanese", label: "" },
  ];

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (validateInputs()) {
      try {
        //    .
        const userData = {
          user_id: inputs.user_id,
          password: inputs.password,
          name: inputs.name,
          phone: inputs.phone,
          nickname: inputs.nickname,
          email: inputs.email,
        };
        await axios.post("http://localhost:3002/api/auth/register", userData);
        /*  const response = await axios.post(
          "http://localhost:3002/api/auth/register",
          userData
        );   response  
        if (response.data.success) {
  //       
  console.log(" :", response.data.message);
}
 , 
*/
        navigate("/login");
      } catch (error) {
        console.error(" :", error);
      }
    }
  };
  const [language, setLanguage] = useState(languageOptions[0]); //   
  const customStyles = {
    indicatorContainer: (provided) => ({
      ...provided,
      paddingLeft: 0,
      paddingRight: 0,
    }),
    // control: , singleValue: , option: , menu:
    singleValue: (styles) => ({
      ...styles,
      color: "#FFFFFF",
      fontSize: "15px",
    }),
    control: (styles) => ({
      ...styles,
      display: "flex",
      minWidth: "107px",
      minHeight: "46px",
      justifyContent: "center",
      alignItems: "center",
      gap: "2px",
      borderRadius: "6px",
      backgroundColor: "#3C3C3C",
      border: "1px",
      boxSizing: "border-box",
      padding: "0 8px",
    }),
    option: (styles) => ({
      ...styles,
      padding: 0,
    }),

    indicatorSeparator: () => ({
      display: "none", //    display none 
    }),
    menu: (styles) => ({
      ...styles,
      backgroundColor: "#292929;", //  (#000000) 
    }),
    valueContainer: (styles) => ({
      ...styles,
      padding: 0,
      textAlign: "right",
    }),
  };
  const handleLanguageChange = (selectedOption) => {
    setLanguage(selectedOption);
  };
  return (
    <div className="background">
      <div className="Register_register-form">
        <mini-container>
          {Object.keys(initialInputs).map((fieldName) => (
            <div className="container_input" key={fieldName}>
              <span className="input_title">{inputLabels[fieldName]}</span>
              <form>
                <input
                  className="Login_textfield"
                  type={
                    fieldName === "password"
                      ? "password"
                      : fieldName === "phone"
                      ? "tel"
                      : "text"
                  }
                  name={fieldName}
                  autoComplete="off"
                  placeholder={inputLabels[fieldName]}
                  value={inputs[fieldName]}
                  onChange={handleInputChange}
                />
                {errors[fieldName] && (
                  <span className="error">{errors[fieldName]}</span>
                )}
              </form>
            </div>
          ))}
          <div className="Login_buttons">
            <button className="orange_btn" onClick={handleSubmit}>
              
            </button>
            <button className="grey_btn">
              <Link to="/login" className="register_text">
                 
              </Link>
            </button>
          </div>
          <div className="conditions_warning">
                ,   
            <span className="conditions"> </span>
            <span className="conditions"></span>
          </div>
        </mini-container>
        <mini-container style={{ marginLeft: "64px" }}>
          <div className="Login_right-section">
            <div className="Login_img_language">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="56"
                height="56"
                viewBox="0 0 56 56"
                fill="none"
              >
                <circle cx="28" cy="28" r="28" fill="#3C3C3C" />

                <text
                  x="50%"
                  y="50%"
                  textAnchor="middle"
                  dominantBaseline="middle"
                  className="only_adult"
                >
                  19
                </text>
              </svg>
              <div>
                <Select
                  value={language}
                  onChange={handleLanguageChange}
                  options={languageOptions}
                  styles={customStyles} //   
                />
              </div>
            </div>
            <p className="Login_alert">
              *   (  )    
                      
               .    [
                   ] [ ]  19
                  .  : ()
               :    223 102 1013 (,
              ) (14548)  : 713-87-01993
              :  2021--0286   : 
            </p>
          </div>
        </mini-container>
      </div>
    </div>
  );
};

export default Resister;
